import axios from 'axios';
import { Module } from 'vuex';
import {
  RootState, KindState, ITypeOrigin, IKindOrigin,
} from './types';

const pastTest: Module<KindState, RootState> = {
  namespaced: true,
  state: {
    typeMenu: [],
    kindMenu: [],
  },
  mutations: {
    SAVE_TYPE_MENU(state, types) {
      state.typeMenu = types.map((d: ITypeOrigin) => ({
        typename: d.Typename,
        title: d.Title,
      }));
    },
    SAVE_KIND_MENU(state, types) {
      state.kindMenu = types.map((d: IKindOrigin) => ({
        kindname: d.Kindname,
        title: d.Title,
      }));
    },
  },
  actions: {
    getTypeMenu({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/TestKind/Type_Menu', {
            publicname: 'jobs',
          })
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_TYPE_MENU', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getKindMenu({ commit }, type) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/TestKind/Kind_Menu', {
            typename: type,
          })
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_KIND_MENU', data.Data);
            resolve(data.Data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
  },
  getters: {

  },
};

export default pastTest;
